header
	@apply sticky top-0 left-0 w-full
	@apply z-10 bg-white -lg:py-1
	.header-language
		@apply flex gap-3.5 body-2
		@apply relative
		&::after
			@apply content-['/'] absolute-center text-utility-gray-300
		.header-language-active
			@apply flex items-center text-utility-gray-600
			li:not(.wpml-ls-current-language)
				@apply hidden
		.header-language-list
			@apply text-utility-gray-300
			li.wpml-ls-current-language
				@apply hidden
	.container-header
		@apply flex-between-center bg-white z-10 -md:py-2
		@apply -lg:container
		.logo
			@apply xl:ml-20 lg:ml-5
		.list-menu-header
			@apply flex-center flex-row xl:rem:gap-x-[76px] gap-x-base rem:max-w-[1262px]
			.header-menu
				@apply lg:block hidden flex-1
				&>ul
					@apply flex-center flex-row xl:rem:gap-x-[76px] gap-x-base
					@apply xl:heading-3 heading-5 font-semibold
					&>li
						@apply relative text-utility-gray-950 
						&::after
							@apply content-[''] w-full h-[2px] rounded-full bg-primary-1
							@apply absolute bottom-0 left-0 translate-y-0.5
							@apply scale-x-0 origin-right
							@apply transition-transform duration-[400ms] ease-linear
						&:hover
							@apply text-primary-1
							&::after
								@apply scale-x-100 origin-left
						&[class*='current'],.active
							&::after
								@apply scale-x-100 origin-left
			.icon-search
				@apply body-1 pointer-events-none
			.btn-contact-header
				@apply xl:rem:min-w-[258px] -lg:hidden transition-300 text-center heading-4 text-utility-white bg-primary-1  lg:px-20 py-6 md:px-10 px-5 hover:bg-primary-5
	.header-mobile-wrapper
		@apply fixed top-[var(--header-height)] left-0 w-screen h-full pointer-events-none z-2 overflow-hidden
		.header-mobile
			@apply absolute top-0 right-0 bg-utility-white translate-x-full transition-300 z-2
			@apply flex flex-col gap-base px-3 h-full lg:w-1/3 md:w-1/2 w-full py-5
			// @apply animate-bounce-in-end animate-once 
			.logo-mobile
				@apply hidden
			.list-menu-header
				.btn-contact-header
					@apply heading-4 text-utility-white bg-primary-4 hover:text-white hover:bg-primary-1 py-6 px-3 transition-300
				&>ul
					@apply flex flex-col gap-5 heading-3 font-normal w-full
					&>li
						a
							@apply relative
							&::after
								@apply content-[''] w-full h-[1px] rounded-full bg-primary-1
								@apply absolute bottom-0 left-0
								@apply scale-x-0 origin-left
								@apply transition-transform duration-300 ease-linear
						&:hover
							a
								@apply text-primary-1
								&::after
									@apply scale-x-100
						&[class*='current']
							a
								&::after
									@apply scale-x-100 origin-left
		.overlay
			@apply absolute top-0 left-0 w-full h-full bg-black/40 -translate-x-full transition-300 z-1 pointer-events-none
			@apply transition-all duration-300 delay-300 ease-in-out
