html
  @apply font-primary

// li
//   @apply border-b border-primary-1

li[class*="current"] 
  @apply text-primary-1

@theme default
  --animate-bounce-in: bounce-in 1s ease-in both
  --animate-bounce-in-down: bounce-in-down 1s ease-in both

@keyframes bounce-in
  0%
    opacity: 0
    transform: scale3d(0.3, 0.3, 0.3)

  100%
    opacity: 1
    transform: scale3d(1, 1, 1)
