.section-banner-home
  @apply -xl:h-screen
  .box-slide
    @apply relative
    .box-image
      @apply -xl:h-screen
      img
        animation: zoomOut 4s ease forwards
      &::after
        content:''
        @apply absolute-full bg-black/20 
    .box-content
      @apply absolute-center text-center text-utility-white w-[70%] -md:w-[95%]
      .sub-title
        @apply heading-1 font-normal
        @media -md
          .sub-title
            @apply rem:text-[30px] font-primary #{!important}
      .title
        // @apply border-t-2 border-linear-2
        @apply relative xl:rem:text-[96px] heading-banner font-medium font-banner mt-6
        &::before
          @apply content-[''] w-full h-[2px] bg-linear-2 rounded-full 
          @apply left-0 -top-3 translate-y-full absolute
  .swiper-pagination-bullet
    @apply bg-utility-white opacity-50 
    @apply mx-0.5 #{!important}
    &.swiper-pagination-bullet-active
      @apply bg-utility-white opacity-100

main
  @apply overflow-x-hidden -xl:w-screen
  // @apply overflow-auto animate-spin duration-1000


@keyframes zoomIn 
  from 
    transform: scale(1)
  
  to 
    transform: scale(1.15)
  

@keyframes zoomOut 
  from 
    transform: scale(1.15)
  
  to 
    transform: scale(1)
  