@import 'src/components/_core/_mixins'
@import 'plugins/Wordpress/form'
html
	@apply overflow-x-hidden
	@apply font-primary
	scroll-behavior: smooth


.edit-link i:before
	content: '✏️'

form[data-status="sent"]
	.wpcf7-response-output
		@apply text-primary-2 border-primary-2 #{!important}
.wpcf7-response-output,.wpcf7-not-valid-tip
	@apply text-[rgb(255,0,0)]
.wpcf7-not-valid-tip
	@apply rem:text-[14px] #{!important}

