.section-home-news
	.wrap
		@apply flex flex-col gap-base
		.box-title
			@apply flex-center flex-col gap-5 
			.tabslet-tab
				@apply flex-nowrap flex gap-3 px-2
				-webkit-overflow-scrolling: touch
				scrollbar-width: none
				-ms-overflow-style: none
				&::-webkit-scrollbar
					display: none
				li
					@apply -md:py-2 flex-shrink-0
					a
						@apply body-2 text-primary-1 border border-primary-1 text-center transition-300
						@apply px-4 py-2 rounded-1 inline-block whitespace-nowrap
					&:hover
						a
							@apply transition-300 text-white border-transparent bg-primary-2
					&:first-child
						a
							@apply px-7
					&:first-child
							@apply ml-auto
					&:last-child
							@apply mr-auto
				.active
					a
						@apply text-white border-transparent bg-primary-2
		.tabslet-content
			@apply relative
			.item-news
				.box-content
					@apply flex-between flex-col gap-3 mt-5
					.title
						@apply heading-4 text-utility-gray-950 line-clamp-2 transition-300
					.decs
						@apply body-3
					.time-news
						@apply body-4 text-utility-gray-500
				&:hover
					.box-content
						.title
							@apply text-primary-1
			.btn
				@apply body-1 px-2.5 py-3 text-white bg-primary-4 xl:absolute-center-y absolute -bottom-10 translate-y-2.5 z-1 rounded-full
				@apply sq-10 flex-center -xl:hidden hover:bg-primary-1
			.btn-next
				@apply right-0 translate-x-20
			.btn-prev
				@apply left-0 -translate-x-20

.swiper-slide-hidden
	display: none !important

.is-disabled
	opacity: 0.4
	cursor: not-allowed
	pointer-events: none
