.section-field-op
	@apply relative bg-utility-gray-50
	.field-op
		@apply relative z-1
		.field-op-list
			&:hover
				@screen xl
					.field-op-item
						.info
							@apply opacity-0 invisible
			.field-op-item
				@apply mr-1
				.info
					@apply body-1 line-clamp-2
					@apply opacity-100 visible xl:opacity-0 xl:invisible
					@media -lg
						@apply opacity-100 visible
					.box-content
						@apply rounded-[0px] #{!important} 
						.content
							@apply body-1 line-clamp-2
				&.active
					.info
						@apply opacity-100 visible
					@screen lg
						@apply flex-[2.667]
						.info
							@apply xl:translate-y-0 
							.title span
								@apply pl-3.5 border-primary-1
							.box-content
								.content
									@apply opacity-100 visible 
									
									@apply line-clamp-none overflow-auto
						.wrap-content-top
							@apply gap-4
							.title
								@apply heading-7 #{!important}
							.icon
								@apply opacity-100 transition-opacity ease-in-out bg-white rounded-full sq-12.5
			.field-op-item
				&:not(:last-child)::before
					@apply content-[''] block absolute right-0 top-0 w-[1.12px] h-full z-2 bg-[linear-gradient(to_bottom,rgba(255,255,255,0),rgba(255,255,255,1),rgba(255,255,255,0))]
				.info
					@apply xl:rem:translate-y-[calc(100%-(73px+(44px*2)))] transition-300 ease-linear 
				.thumb
					&::before
						background: linear-gradient(0deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.20) 100%)
						@apply content-[''] absolute bottom-0 left-0 w-full h-full block z-1 transition-300 ease-linear 
						// @apply pointer-events-none
				.wrap-content-top
					@apply xl:rem:min-h-[134px]
					.title
						@apply heading-3 #{!important} 
						@apply delay-200
					.icon
						@apply aspect-square relative sq-0 -lg:sq-10 -lg:aspect-square inline-flex items-center justify-center bg-white rounded-full opacity-100 lg:opacity-0 transition-500 ease-linear
						// @apply pointer-events-none
						&:hover
							&::before, &::after
								@apply text-primary-1
								@apply translate-x-full opacity-0
							&::after
								@apply translate-x-0 opacity-100 font-bold
						&::before,&::after
							content: "\f061"
							@apply font-awesome text-lg inline-block transition-all absolute font-normal leading-none duration-300
						&::after
							@apply -translate-x-full opacity-0 


