.section-home-contact
	@apply text-white section-py relative -md:rem:min-h-[860px]
	&::after
		content: ""
		@apply bg-gradient-bg-1 opacity-40 z-1 absolute inset-0
	.title-bg
		@apply xl:rem:text-[64px] heading-1-mb	font-banner text-center relative z-2
	.box-contact
		@apply	flex flex-col gap-5 p-8 md:rem:w-[528px] w-auto justify-start items-start
		@apply bg-black/15 backdrop-blur-[10px] rounded-[20px] relative z-2
		box-shadow: 12px 15px 13.9px 0 rgba(0, 0, 0, 0.10)
		.contact-phone,.contact-mail
			@apply flex items-center gap-3
			.fa-phone,.fa-envelope
				@apply rem:text-[40px]
			.phone,.mail
				@apply xl:rem:text-[40px] heading-7 transition-all duration-100 
				&:hover
					@apply text-primary-1

		.form-contact

			.title-contact
				@apply mb-5
			.form-group
				.wpcf7-not-valid-tip
					@apply translate-y-0 
					position: unset
				input::placeholder,textarea::placeholder
					@apply text-white
				input,textarea
					@apply bg-transparent focus:outline-none w-full
					@apply bg-black/25 py-3 px-6 rounded-1 
				textarea
					@apply rem:h-[144px]
				&:last-child
					@apply col-span-full
					
			.btn 
				input
					@apply cursor-pointer py-3 px-6 bg-white text-primary-1 transition-all-linear-300 w-fit body-3 font-bold rounded-1
					&:hover
						@apply bg-primary-2 text-white
