footer
  @apply section-py flex flex-col gap-8 
  .box-title
    @apply flex-1
    .box-logo-footer
      @apply rem:w-[477px] -md:rem:w-[270px]
      .logo-footer
        @apply img-ratio ratio:pt-[75_477] 
    .sub-title
      @apply heading-2 -md:heading-7
  .box-title, .form-email
    @apply flex flex-col gap-2
  .form-email
    @apply lg:rem:w-[460px]
    form.submitting
      button
        &:before
          @apply absolute-center m-0 #{!important} 
        &::after
          @apply opacity-0
    .btn-footer-form
      @apply relative
      &::after
        content: "\f1d8"
        @apply absolute-center font-awesome font-normal
      &::before
        @apply animate-spin-circle
    .footer-form
      @apply relative flex-between-center text-utility-gray-500 bg-utility-gray-100 py-1 pr-1 rounded-1 w-full
      @apply rem:min-h-[56px]
      .wpcf7-form-control-wrap
        @apply -lg:w-[80%]
        .input-contact-footer
          @apply pl-2.5 bg-utility-gray-100 body-2 flex-1 focus:outline-none w-full
      .wpcf7-not-valid-tip
        @apply translate-y-[50%] top-full absolute
    .wpcf7-response-output
      @apply text-primary-2 border-primary-2 mt-6 #{!important}
  .link-footer
    @apply lg:py-8 py-5 border-y border-utility-gray-100 text-utility-gray-950
    @apply flex-between flex-wrap gap-y-5 -md:flex-col -lg:justify-start items-baseline  
    .location-info
      @apply xl:rem:w-[626px] w-full
      .certificates-gallery
        @apply overflow-hidden w-full 
        .swiper-footer-logo
          .swiper-slide
            .item-gallery
              @apply rem:w-[137px] 
              .box-image
                @apply img-ratio ratio:pt-[86_137] 
  ul
    li
      strong,p
        @apply inline
      a
        @apply relative
        &::after
          @apply content-[''] w-full  rem:h-[1px] rounded-full bg-primary-1
          @apply absolute bottom-0 left-0 translate-y-0.5
          @apply scale-x-0 origin-right
          @apply transition-transform duration-[400ms] ease-linear
        &:hover
          @apply text-primary-1 after:scale-x-100  after:origin-left
          
      &[id*='menu-item']
        a
          @apply relative
          &::after
            @apply content-[''] w-full h-[1px] rounded-full bg-primary-1
            @apply absolute bottom-0 left-0
            @apply scale-x-0 origin-right
            @apply transition-transform duration-[400ms] ease-linear
          &:hover
            @apply text-primary-1
            &::after
              @apply scale-x-100 origin-left
      &[class*='current']
        a
          &::after
              @apply scale-x-100 origin-left 
  .social
    ul
      li
        a
          @apply pointer-events-auto
  .quick-link,.our-products-footer,.social
    @apply -lg:w-1/3